.text-info {
  padding: 40px 0;
  background: $color-gray-3;
  &__item {
    &_white {
      .container {
        .col-6 {
          background: $color-white; } } }
    &_blue {
      .container {
        .col-6 {
          background: linear-gradient(212.28deg, $color-blue-7 11.16%, $color-blue-8 77.95%); } } }
    &_red {
      .container {
        .col-6 {
          background: linear-gradient(180deg, $color-red 0%, $color-red-2 100%); } } } }
  &__title {
    font: 24px/1.333 $font-helvetica-bold;
    padding: 56px 41px 16px 41px;
    .text-info__item_white & {
      color: $color-blue-6; }
    .text-info__item_blue & {
      color: $color-white; }
    .text-info__item_red & {
      color: $color-white; } }
  &__text {
    font: 15px/1.6 $font-helvetica-regular;
    padding: 0 41px 15px 41px;
    .text-info__item_white & {
      color: $color-black-2; }
    .text-info__item_blue & {
      color: $color-white; }
    .text-info__item_red & {
      color: $color-white; } }
  &__link {
    font: 15px/1.6 $font-helvetica-regular;
    text-decoration: none;
    padding: 0 0 0 41px;
    .text-info__item_white & {
      color: $color-blue-5; }
    .text-info__item_blue & {
      color: $color-white;
      opacity: 0.6; }
    .text-info__item_red & {
      color: $color-white;
      opacity: 0.6; } }
  &__link-img {
    display: block;
    text-decoration: none; }
  &__img {
    width: 100%;
    height: auto;
    display: block; }
  .row.no-gutters {
    margin-right: 0;
    margin-left: 0;
    &>[class^="col-"],
    &>[class*=" col-"] {
      padding-right: 0;
      padding-left: 0; } } }
