.map {
  width: 100%;
  height: 431px;
  position: relative;
  &__container {
    width: 100%;
    height: 431px;
    z-index: 1; }
  &__descr-wrapper {
    z-index: 2;
    width: 100%;
    max-width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%,0); }
  &__descr {
    width: 100%;
    max-width: 586px;
    background: $color-white;
    box-shadow: 0px 10px 20px rgba($color-gray-5, 0.3);
    padding: 40px 40px 33px; }
  &__title {
    font: 24px/1.333 $font-helvetica-bold;
    color: $color-blue-6;
    margin: 0 0 18px; }
  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &:nth-child(2) {
      margin: 0 0 18px; }
    &:nth-child(3) {
      margin: 0 0 14px; } }
  &__col {
    &_indent {
      margin: 0 30px 0 0; } }
  &__subtitle {
    font: 13px/1.538 $font-helvetica-regular;
    color: $color-gray-6;
    margin: 0 0 4px; }
  &__link {
    font: 15px/1.6 $font-helvetica-regular;
    color: $color-blue-11;
    text-decoration: none;
    position: relative;
    padding: 0 0 0 21px;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0; }
    &_address {
      align-items: flex-start;
      &:before {
        @extend .icon-marker;
        top: 4px; } }
    &_email {
      &:before {
        @extend .icon-mail-blue-2;
        top: 4px; } }
    &_phone {
      &:before {
        @extend .icon-phone;
        top: 1.5px; } } }
  &__text {
    font: 13px/1.538 $font-helvetica-regular;
    color: $color-gray-6; } }
