.header {
  &__row {
    &_top {
      height: 40px;
      background: $color-blue;
      display: flex;
      align-items: center;
      .container {
        display: flex;
        justify-content: flex-start;
        align-items: center; } }
    &_bottom {
      height: 60px;
      background: $color-white;
      display: flex;
      align-items: center;
      .container {
        display: flex;
        justify-content: flex-start;
        align-items: center; } } }
  &__select {}
  &__select-title {
    font: 13px/1.538 $font-helvetica-regular;
    color: $color-white;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 7px 0 8px;
    cursor: pointer;
    &:before {
      content: '';
      @extend .icon-squares;
      margin: 0 7px 0 0; }
    &:after {
      content: '';
      @extend .icon-arrow-white-bottom;
      margin: 0 0 0 8px; } }
  &__links {
    margin: 0 0 0 auto;
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  &__link {
    font: 13px/1.538 $font-helvetica-regular;
    color: $color-white;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &_version {
      &:before {
        content: '';
        @extend .icon-eye;
        margin: 0 6px 0 0; }
      &:after {
        content: '';
        width: 1px;
        height: 20px;
        background: rgba($color-white, 0.2);
        opacity: 0.5;
        margin: 0 17px 0 17px; } }
    &_help {
      &:before {
        content: '';
        @extend .icon-help;
        margin: 0 6px 0 0; } }
    &_search {
      font: 15/1.6 $font-helvetica-regular;
      color: $color-blue-2;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-search;
        margin: 0 8px 0 0; }
      &:after {
        content: '';
        display: block;
        width: 1px;
        height: 40px;
        background: rgba($color-gray, 0.5);
        margin: 0 20px 0 20px; } }
    &_office {
      font: 15/1.6 $font-helvetica-regular;
      color: $color-blue-2;
      &:before {
        content: '';
        @extend .icon-social;
        margin: 0 8px 0 0; }
      &:after {
        content: '';
        @extend .icon-enter;
        margin: 0 0 0 12px; } } }
  &__logo {
    @extend .icon-logo;
    margin: 0 20px 0 0; }
  &__nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto 0 0;
    &:before {
      content: '';
      display: block;
      width: 1px;
      height: 40px;
      background: rgba($color-gray, 0.5);
      margin: 0 20px 0 0; } }
  &__nav-item {
    font: 15px/1.6 $font-helvetica-regular;
    color: $color-blue-2;
    text-decoration: none;
    margin: 0 30px 0 0;
    transition: color 0.25s ease;
    &:last-child {
      margin: 0 0 0 0; }
    &:hover {
      color: $color-red; } } }
