.services {
  background: $color-gray-3;
  padding: 70px 0 40px;
  position: relative;
  z-index: 1;
  ._hidden {
    display: none;
    visibility: hidden; }
  &__item-wrapper {}
  &__item {
    background: $color-white-2;
    height: 108px;
    margin: 0 0 30px;
    text-decoration: none;
    display: block;
    padding: 20px;
    border-radius: 2px;
    box-shadow: 0px 0px 10px $color-gray-2;
    position: relative;
    transition: all 0.5s ease, background 0.8s linear;
    cursor: pointer;
    &:before {
      position: absolute; }
    .services__item-wrapper:hover & {
      transform: translate3d(0,-10px,0);
      box-shadow: 0px 15px 15px rgba($color-gray-4, 0.7);
      background: linear-gradient(180deg, $color-blue-3 0%, #0845B2 100%); }
    &_recommendations {
      &:before {
        content: '';
        @extend .icon-people-blue;
        top: 25px;
        right: 25px; }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-people-white; } } }
    &_statistics {
      &:before {
        content: '';
        @extend .icon-chart-blue;
        top: 25px;
        right: 23px; }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-chart-white; } } }
    &_centers {
      &:before {
        content: '';
        @extend .icon-card-blue;
        top: 25px;
        right: 25px; }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-card-white; } } }
    &_find {
      &:before {
        content: '';
        @extend .icon-search-large-blue;
        top: 25px;
        right: 25px; }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-search-large-white; } } }
    &_certificates {
      &:before {
        content: '';
        @extend .icon-files-blue;
        top: 25px;
        right: 25px; }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-files-white; } } }
    &_appeal {
      &:before {
        content: '';
        @extend .icon-mail-blue;
        top: 25px;
        right: 25px; }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-mail-white; } } } }
  &__title {
    font: 18px/1.333 $font-helvetica-regular;
    color: $color-black-2;
    transition: color 0.35s ease;
    .services__item-wrapper:hover & {
      color: $color-white-2; } }
  &__arrow {
    position: absolute;
    left: 20px;
    bottom: 16px;
    width: 21px;
    height: 8px;
    overflow: hidden;
    transition: width 0.4s ease;
    .services__item-wrapper:hover & {
      width: 51px; }
    &:before {
      content: '';
      width: 30px;
      height: 1px;
      background: $color-white-2;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate3d(-100%,-50%,0);
      transition: transform 0.4s ease; }
    &:after {
      content: '';
      @extend .icon-arrow-blue-right;
      position: absolute;
      left: 0;
      top: 0;
      transition: left 0.4s ease; }
    .services__item-wrapper:hover & {
      &:before {
        transform: translate3d(0,-50%,0); }
      &:after {
        left: 30px;
        @extend .icon-arrow-white-right; } } }
  &__btn {
    width: 100%;
    text-decoration: none;
    display: block;
    height: 48px;
    box-shadow: 0px 0px 10px $color-gray-2;
    background: $color-white;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font: 15px/1.6 $font-helvetica-regular;
    color: $color-blue-5;
    &:after {
      content: '';
      @extend .icon-arrow-blue-bottom;
      margin: 0 0 0 10px; }
    &_hidden {
      visibility: hidden;
      display: none; } } }
