// Import fonts
// PT Sans
// @import url('https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i&subset=cyrillic-ext')
// PlayFairDisplay
@include font-face(HelveticaRegular, "../fonts/Helvetica_400", 400, normal, eot woff2 woff svg ttf);
@include font-face(HelveticaBold, "../fonts/Helvetica_600", 600, normal, eot woff2 woff svg ttf);
html {
    -webkit-font-smoothing: antialiased; }

body {
    font-family: $font-helvetica-regular;
    font-size: 14px;
    line-height: 1.4;
    color: $color-black;
    text-rendering: optimizeLegibility;
    -webkit-text-rendering: optimizeLegibility;
    @include selectionText; }
