.qualifications {
  padding: 58px 0 53px;
  background-size: cover;
  background-image: url('../img/bgs/bg-qualifications.png');
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
  z-index: 2;
  &__item {
    .col-7 {
      position: relative; }
    &:first-child {
      .col-3 {
        border-bottom: 1px rgba($color-white, 0.2) solid;
        padding-bottom: 34px;
        margin-bottom: 36px; }
      .col-7 {
        border-bottom: 1px rgba($color-white, 0.2) solid;
        padding-bottom: 34px;
        margin-bottom: 36px;
        &:before {
          content: '';
          @extend .icon-qualification;
          position: absolute;
          left: 15px;
          top: 0; } } }
    &:last-child {
      .col-7 {
        &:before {
          content: '';
          @extend .icon-profile;
          position: absolute;
          left: 21px;
          top: -3px; } } } }
  &__title {
    font: 18px/1.333 $font-helvetica-bold;
    color: $color-white;
    margin: 0 0 10px 100px; }
  &__text {
    font: 13px/1.538 $font-helvetica-regular;
    color: $color-white;
    margin: 0 0 0 100px; }
  &__btn {
    border: 1px solid $color-white;
    border-radius: 2px;
    width: 270px;
    height: 48px;
    font: 18px/1 $font-helvetica-regular;
    color: $color-white;
    background: transparent;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 10px 0 0 0;
    position: relative;
    transition: all 0.25s ease;
    &:after {
      content: '';
      @extend .icon-arrow-white-right-2;
      position: absolute;
      right: 27px; }
    &:before {
      content: '';
      position: absolute;
      right: 22px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      width: 20px;
      height: 20px;
      background: linear-gradient(180deg, $color-red 0%, $color-red-2 100%);
      border-radius: 10px;
      opacity: 0;
      transition: all 0.25s ease; }
    &:hover {
      background: $color-white;
      color: $color-blue-6;
      &:before {
        opacity: 1; } }
    .qualifications__item:first-child & {
      padding: 0 0 0 25px; }
    .qualifications__item:last-child & {
      padding: 0 0 0 51px; } } }
