.footer {
  &__row {
    &_top {
      background: $color-blue-6;
      padding: 41px 0 34px 0;
      .col-12 {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start; } }
    &_bottom {
      background: $color-blue-10;
      height: 40px;
      display: flex;
      align-items: center;
      .col-12 {
        display: flex;
        justify-content: flex-start;
        align-items: center; } } }
  &__nav {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    max-width: 734px;
    position: relative;
    &:before {
      content: '';
      display: block;
      width: 1px;
      height: 133px;
      background: rgba($color-white,0.2);
      position: absolute;
      right: 0;
      top: -4px; } }
  &__item {
    padding: 0 35px 0 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    &_indent {
      padding: 34px 0 0 0; }
    &:first-child {
      width: 30.7%; }
    &:nth-child(2) {
      width: 35%; }
    &:last-child {
      width: 33.333%;
      padding-right: 0; } }
  &__title {
    text-transform: uppercase;
    font: 12px/1.25 $font-helvetica-bold;
    color: rgba($color-white, 0.4);
    margin: 0 0 19px; }
  &__link {
    text-decoration: none;
    font: 15px/1.666 $font-helvetica-regular;
    color: $color-white;
    margin: 0 20px 8px 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    &:last-child {
      margin-bottom: 0; }
    .footer__contacts-item &:last-child {
      margin-right: 0; }
    &_email {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-mail-white-2;
        margin: 0 7px 0 0; } }
    &_question {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-help-2;
        margin: 0 7px 0 0; } }
    &_blank {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-form;
        margin: 0 7px 0 0; } } }
  &__contacts {
    padding: 0 0 0 50px; }
  &__contacts-item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start; }
  &__copyright {
    &_transparent {
      font: 13px/1.538 $font-helvetica-regular;
      color: rgba($color-white, 0.4);
      margin: 0 12px 0 0; }
    &_white {
      font: 13px/1.538 $font-helvetica-regular;
      color: $color-white; } } }
