.statistics {
  padding: 26px 0 24px;
  background: $color-white;
  box-shadow: 0px 0px 30px rgba($color-blue-9, 0.8);
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  &__item-number {
    font: 48px/1 $font-helvetica-bold;
    color: $color-blue-6;
    border-bottom: 2px $color-red solid;
    width: 100%;
    text-align: center;
    padding: 0 0 4px;
    margin: 0 0 10px; }
  &__item-descr {
    font: 15px/1.6 $font-helvetica-regular;
    color: $color-blue-6;
    text-align: center; } }
